// BlogCard Sub-Component
function BlogCard({ blog }) {
  return (
    <Link href={`/blog/${blog.slug}`}>
      <article className="group h-full bg-slate-800 rounded-xl overflow-hidden shadow-lg hover:shadow-2xl transition-all duration-300 hover:-translate-y-2 cursor-pointer border border-slate-700 hover:border-blue-500">
        {/* Image Container */}
        <div className="relative h-48 overflow-hidden bg-slate-700">
          <Image
            src={blog.image}
            alt={blog.title}
            width={400}
            height={200}
            className="w-full h-full object-cover group-hover:scale-110 transition-transform duration-300"
            priority={blog.id <= 3}
          />
          {/* Category Badge */}
          <div className="absolute top-3 right-3">
            <span className="inline-block bg-gradient-to-r from-blue-500 to-cyan-500 text-white text-xs font-semibold px-3 py-1 rounded-full">
              {blog.category}
            </span>
          </div>
        </div>

        {/* Content */}
        <div className="p-6 flex flex-col h-full">
          {/* Title */}
          <h2 className="text-xl font-bold text-white mb-3 line-clamp-2 group-hover:text-blue-400 transition-colors">
            {blog.title}
          </h2>

          {/* Description */}
          <p className="text-slate-400 text-sm mb-4 line-clamp-3 flex-grow">
            {blog.description}
          </p>

          {/* Meta Info */}
          <div className="space-y-3 border-t border-slate-700 pt-4">
            <div className="flex items-center justify-between text-xs text-slate-500">
              <span>{blog.readTime}</span>
              <span>{blog.date}</span>
            </div>
            <div className="flex items-center justify-between">
              <span className="text-sm font-medium text-slate-300">By {blog.author}</span>
              <span className="text-blue-400 group-hover:text-blue-300 transition-colors font-semibold text-sm">
                Read More →
              </span>
            </div>
          </div>
        </div>
      </article>
    </Link>
  );
}

